.item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-top: 1px solid hsl(275, 100%, 97%);
 
}

.question {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    line-height: normal;
    margin: 25px 0 15px 0;
}

.question p { 
    font-family: 'Work Sans', sans-serif;
    color: hsl(292, 42%, 14%);
    font-size: 16px;
    font-weight: 800;
    text-align: left;
    cursor: pointer;
}

.question p:hover {
    color: #AD28EB;
}

.btn-open {
    width: 25px;
    margin-left: auto;
    cursor: pointer;
}

.btn-close {
    display: none;
}

.answer {
    display: block;
    position: relative;
    font-family: 'Work Sans', sans-serif;
    font-weight: 300;
    color: hsl(292, 16%, 49%);
    font-size: 16px;
    margin: 0px auto 40px auto;
    text-align: left;
}

.hide {
    display: none;
    background-color: pink;
}

@media only screen and (max-width: 768px) {
    .container {
        width: 90%;
        padding: 25px;
        overflow: scroll;
        height: fit-content;
        margin: 20px auto;
    }

    .question p {
        padding: 10px 15px 10px 0;
    }
}