* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100%;
    background-color: hsl(275, 100%, 97%);
}

.container {
    display: block;
    position: absolute;
    max-width: 650px;
    background-color: #fff;
    padding: 35px 20px 35px 30px;
    box-sizing: border-box;
    top: 53%;
    transform: translateY(-50%);
    margin: 0 auto;
    border-radius: 15px;
    left: 0; 
    right: 0;
}

.header {
    display: flex;
    flex-direction: row;
    margin: 15px auto 35px auto;
}

.star {
    width: 60px;
    padding: 0 15px 0 10px;
}

h1 {
    font-family: 'Work Sans', sans-serif;
    font-weight: 800;
    font-size: 3rem;
    color:  hsl(292, 42%, 14%);
}
